body {
  scroll-behavior: smooth;
  margin: 0px;
  padding: 0px;
  background-color: #4c164c;
  background-image: linear-gradient(90deg, rgb(35, 11, 48) 0%, rgb(60, 12, 98) 48%, rgb(56, 20, 98) 64%, rgb(63, 25, 98) 83%, rgb(74, 47, 96) 100%);
  overflow-x: hidden;
}

.Navmax_container {
  background-image: linear-gradient(90deg, rgb(35, 11, 48) 0%, rgb(60, 12, 98) 48%, rgb(56, 20, 98) 64%, rgb(63, 25, 98) 83%, rgb(74, 47, 96) 100%);
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 100;
  box-shadow: 1px 3px 3px rgba(0, 0, 0, 0.2392156863);
}
.Navmax_container .Navmax {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Navmax_container .Navmax .logo_container {
  width: 100%;
  display: grid;
}
.Navmax_container .Navmax .logo_container img {
  width: 50%;
  margin: 0 auto;
}
.Navmax_container .Navmax .burger {
  cursor: pointer;
  width: 30px;
  height: auto;
}
.Navmax_container .Navmax .burger .linee {
  width: 100%;
  height: 2px;
  background-color: rgb(239, 232, 240);
  margin: 5px 0;
  transition: 0.4s;
}
.Navmax_container .Navmax .contact {
  cursor: pointer;
  border: none;
  background-color: transparent;
  border: 1px solid 1px solid rgba(44, 39, 48, 0.59);
  border-radius: 10px;
  color: wheat;
  margin-right: 5px;
  padding: 5px 8px;
  box-shadow: 2px 2px 3px rgba(191, 181, 181, 0.11);
}
.Navmax_container .burger:active .linee:nth-child(1) {
  transform: rotate(-45deg) translate(-5px, 6px);
}
.Navmax_container .burger.hover .linee:nth-child(2) {
  opacity: 0;
}
.Navmax_container .burger:active .linee:nth-child(3) {
  transform: rotate(45deg) translate(-5px, -6px);
}
.Navmax_container .burger_menu {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  transition: 200ms ease-in;
}
.Navmax_container .burger_menu a {
  border-bottom: 0.2px solid rgba(245, 240, 240, 0.281);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  font-family: sans-serif;
  color: white;
  font-size: 18px;
  padding: 5px;
  text-decoration: none;
  animation: fly 2s forwards;
  opacity: 0;
}
@keyframes fly {
  0% {
    transform: translateY(0) translateX(0);
    opacity: 0;
  }
  50% {
    transform: translateY(-200px) translateX(200px);
  }
  100% {
    transform: translateY(-400px) translateX(400px);
    opacity: 1;
  }
}

