.isVis .intro_card {
  animation: isvis 2s forwards;
  opacity: 0;
}
.isVis .Line {
  animation: Linew 3s forwards;
  animation-delay: 1;
}
.isVis h1 {
  animation: Opatext 3s forwards;
  animation-delay: 3;
}
@keyframes Opatext {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes Linew {
  from {
    width: 3%;
  }
  to {
    width: 70%;
  }
}
@keyframes isvis {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

